/***** Accordion ****/
dl.accordion {
  position: relative;
  margin: 30px 0;
  @media ($mq-tablet) {
  margin: 60px 0;
}

  dt {
    background-color: $color-white-solid;
    color: $color-black-solid;
    border: 1px solid $color-black-solid;
    padding: 15px;
    margin-top: 10px;
    width: 100%;
    text-align: left;
    display: block;
    @include transition();

    &:first-of-type {
      margin-top: 0px;
    }

    // HOVER
    &:hover {
      cursor: pointer;
      background-color: $color-grey-solid;
    }

    // AKTIVES ACCORDION
    &.active {
      background-color: $color-black-solid;
      color: $color-white-solid;
      margin-bottom: 0px;

      svg.icon {
        fill: $color-white-solid;
        @include rotate(90deg);
      }
    }

    // first element (e.g. h2, h3)
    > *:first-child {
      margin: 0;
      display: inline-block;
    }

    // Icons (e.g. arrow-down)
    svg.icon {
      width: 25px;
      float: right;
    }
  }

  dd {
    background-color: $color-grey-solid;
    -webkit-margin-start: 0px;
    -moz-margin-start: 0px;
    overflow: hidden;
    max-height: 0px;
    height: auto;
    padding: 0 30px;

    &.active {
      max-height: 2000px;
      @include transition(max-height, ease, 2s); // aufklappen transition
    }

    > *:first-child {
      margin-top: 30px;
    }

    > *:last-child {
      margin-bottom: 30px;
    }

  }

}


