@keyframes vanishIn {
  0% {
    opacity: 0;
    transform: scale(1.3, 1.3);
    visibilty: hidden;
  }

  100% {
    opacity: 1;
    transform: scale(1, 1);
    visibilty: visible;
  }
}

@keyframes vanishIn {
  0% {
    opacity: 0;
    -webkit-transform: scale(1.3, 1.3);
    visibilty: hidden;
  }

  100% {
    opacity: 1;
    -webkit-transform: scale(1, 1);
    visibilty: visible;
  }
}

@keyframes vanishOut {
  0% {
    opacity: 1;
    transform: scale(1, 1);
    visibilty: visible;
  }

  100% {
    opacity: 0;
    transform: scale(1.1, 1.1);
    visibilty: hidden;
  }
}

@keyframes vanishOut {
  0% {
    opacity: 1;
    -webkit-transform: scale(1, 1);
    visibilty: visible;
  }

  100% {
    opacity: 0;
    -webkit-transform: scale(1.1, 1.1);
    visibilty: hidden;
  }
}

@keyframes slideInDown {
  from {
    transform: translate3d(0, -10%, 0);
    visibility: visible;
    opacity: 0;
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20%);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.95, 0.95, 0.95);
  }

  50% {
    opacity: 1;
  }
}

.zoomIn {
  animation-name: zoomIn;
  animation-duration: .35s;
}

.slideDown {
  animation-name: slideInDown;
  animation-duration: .35s;
  animation-fill-mode: both;
}

