.btn {
  display: table;
  position: relative;
  font-size: 1em;
  line-height: 1em;
  text-align: center;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  margin-top: 1em; // 16px
  margin-bottom: 1em; // 16px

  /********************************/
  /**** rectangle button start ****/
  &-rect {
    width: auto;
    height: auto;
    padding: 1.5em 4.5em; // 24px 72px
    box-sizing: content-box;

    // default is purple
    background-color: $color-purple-solid;
    color: $color-white-solid;
    &:hover {
      background-color: $color-white-solid;
      color: $color-purple-solid;
    }

  }

  // it can also be a white button
  &-white {
    background-color: $color-white-solid;
    color: $color-black-solid;
    &:hover {
      color: $color-purple-solid;
    }
  }
  /**** rectangle button end ****/
  /********************************/

  /**************************************/
  /**** rounded corners button start ****/
  &-more {
    width: auto;
    height: auto;
    box-sizing: content-box;
    border-radius: 2em; // 32px
    padding: 1.5em 2.5em; // 24px 40px
    background-color: $color-grey-light;
    color: $color-black-solid;
    &:hover {
      color: $color-purple-solid;
    }
  }
  /**** rounded corners button end ****/
  /************************************/

  /*****************************/
  /**** filter button start ****/
  &-filter {
    width: auto;
    height: auto;
    font-size: .875em; // 14px
    line-height: .875em; // 14px
    padding: .375em .875em; // 6px 14px
    box-sizing: content-box;
    border-radius: 2em; // 32px
    background-color: $color-grey-light;
    color: $color-black-solid;
    border: 2px solid transparent; // pseudo-border

    &:hover {
      color: $color-purple-solid;
    }

    &.active {
      background-color: $color-white-solid;
      border: 2px solid $color-purple-solid;
    }
  }
  /**** filter button end ****/
  /***************************/

  /***************************/
  /**** down button start ****/
  &-down {
    width: 4em;
    height: 4em;
    border-radius: 100%;
    padding: 1.5em; // 24px
    box-sizing: border-box;

    // default is purple
    background-color: $color-purple-solid;
    color: $color-white-solid;

    &:before {
      content:' ';
      position: absolute;
      left: 50%;
      top: 50%;
      @include translate(-50%, -50%);
      @include transition();
      background-image: url("../../assets/img/icons/spritesheet/svg/sprite.svg");
      background-repeat: no-repeat;
      background-size: 24px 288px;
      background-position: 0 -24px;
      width: 24px;
      height: 24px;
    }

    // move the arrow-icon down
    &:hover {
      &:before {
        @include translate(-50%, -30%);
      }
    }
  }
  /**** rectangle button end ****/
  /******************************/

}


/*********************************************************************/

