//+++++ plus icon start +++++//
.plus {
  &:before,
  &:after{
    content: "";
    position: absolute;
    background-color: $color-black-solid;
    @include vh-align();
    @include transform-origin(); // 50% 50%
    @include transition();
  }

  /* Vertical line */
  &:before{
    width: 4px;
    height: 15px;
  }

  /* horizontal line */
  &:after{
    width: 15px;
    height: 4px;
  }

}
